.page-header {
  margin-bottom: 28px;
  padding: 20px 30px;

  background-color: $color-secondary;

  @media (min-width: $screen-tablet) {
    margin-bottom: 56px;
    padding: 40px 0;
  }

  &__title {
    margin: 0;

    font-size: 24px;
    line-height: 1.25em;
    color: #ffffff;

    @media (min-width: $screen-tablet) {
      max-width: 710px;
      width: 100%;
      margin: 0 auto;
    }
  }
}
