.page-main {
  padding: 0 30px;

  &__inner-wrapper {
    @media (min-width: $screen-tablet) {
      max-width: 868px;
      width: 100%;
      margin: 0 auto;
      padding: 56px;
      padding-top: 48px;

      box-shadow: 0 4px 30px rgba(0, 0, 0, 0.04);
      border-radius: 10px;
    }
  }

  &__row {
    display: none;

    @media (min-width: $screen-tablet) {
      display: flex;
      align-items: center;
      justify-content: space-between;

      max-width: 500px;
      width: 100%;
      margin-bottom: 40px;
    }
  }

  &__info-text {
    @media (min-width: $screen-tablet) {
      display: block;
      margin: 0;

      font-size: 18px;
      line-height: 23px;
      color: #000000;
      text-align: left;
    }

    &--bold {
      @media (min-width: $screen-tablet) {
        max-width: 240px;
        width: 100%;

        font-weight: 500;
      }
    }

    &:first-of-type {
      @media (min-width: $screen-tablet) {
        max-width: 200px;
        width: 100%;
      }
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;

    font-family: "SF UI Display", sans-serif;
    letter-spacing: 0.16px;

    @media (min-width: $screen-tablet) {
      max-width: 710px;
      width: 100%;
      margin: 0 auto;
      margin-bottom: 20px;

      font-size: 18px;
      line-height: 19px;
    }

    &:last-of-type {
      margin-bottom: 44px;
    }

    &--bold {
      color: #000000;

      @media (min-width: $screen-tablet) {
        color: #373737;
      }
    }
  }
}
