html {
	box-sizing: border-box;
	scroll-behavior: smooth;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	position: relative;

	min-width: 320px;
	margin: 0;
	padding: 0;

	font-family: $font-family;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: 400;
	color: $color-main;

	overflow-x: hidden;
}
