@font-face {
  font-family: 'Geometria';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/geometria700.woff2") format("woff2"), url("../fonts/geometria700.woff") format("woff"); }

@font-face {
  font-family: 'Geometria';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/geometria500.woff2") format("woff2"), url("../fonts/geometria500.woff") format("woff"); }

@font-face {
  font-family: 'Geometria';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/geometria400.woff2") format("woff2"), url("../fonts/geometria400.woff") format("woff"); }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Geometria", "SF UI Display", sans-serif;
  font-size: 15px;
  line-height: 1.27em;
  font-weight: 400;
  color: #8D8D8D;
  overflow-x: hidden; }

.page-header {
  margin-bottom: 28px;
  padding: 20px 30px;
  background-color: #1C83FF; }
  @media (min-width: 768px) {
    .page-header {
      margin-bottom: 56px;
      padding: 40px 0; } }
  .page-header__title {
    margin: 0;
    font-size: 24px;
    line-height: 1.25em;
    color: #ffffff; }
    @media (min-width: 768px) {
      .page-header__title {
        max-width: 710px;
        width: 100%;
        margin: 0 auto; } }

.field-text {
  display: block;
  max-width: 315px;
  width: 100%;
  margin-bottom: 32px;
  font-family: "SF UI Display", sans-serif; }
  @media (min-width: 768px) {
    .field-text {
      max-width: 500px;
      margin-bottom: 40px;
      font-size: 18px;
      line-height: 23px;
      color: #000000; } }
  .field-text__input {
    display: block;
    width: 100%;
    margin-top: 24px;
    padding: 13px;
    font: inherit;
    border: none;
    background: #FBFBFB;
    box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.04);
    border-radius: 30px; }
    @media (min-width: 768px) {
      .field-text__input {
        margin-top: 10px;
        padding: 11px;
        box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.1); } }

.payment-form__button {
  display: block;
  max-width: 315px;
  width: 100%;
  margin: 0;
  padding: 13px;
  font-family: "SF UI Display", sans-serif;
  font-size: 18px;
  line-height: 19px;
  font-weight: 500;
  letter-spacing: 0.16px;
  color: #ffffff;
  border: none;
  background-color: #5BA550;
  border-radius: 54px;
  cursor: pointer; }
  @media (min-width: 768px) {
    .payment-form__button {
      padding: 22px; } }

.page-main {
  padding: 0 30px; }
  @media (min-width: 768px) {
    .page-main__inner-wrapper {
      max-width: 868px;
      width: 100%;
      margin: 0 auto;
      padding: 56px;
      padding-top: 48px;
      box-shadow: 0 4px 30px rgba(0, 0, 0, 0.04);
      border-radius: 10px; } }
  .page-main__row {
    display: none; }
    @media (min-width: 768px) {
      .page-main__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 500px;
        width: 100%;
        margin-bottom: 40px; } }
  @media (min-width: 768px) {
    .page-main__info-text {
      display: block;
      margin: 0;
      font-size: 18px;
      line-height: 23px;
      color: #000000;
      text-align: left; } }
  @media (min-width: 768px) {
    .page-main__info-text--bold {
      max-width: 240px;
      width: 100%;
      font-weight: 500; } }
  @media (min-width: 768px) {
    .page-main__info-text:first-of-type {
      max-width: 200px;
      width: 100%; } }
  .page-main__text {
    margin: 0;
    margin-bottom: 20px;
    font-family: "SF UI Display", sans-serif;
    letter-spacing: 0.16px; }
    @media (min-width: 768px) {
      .page-main__text {
        max-width: 710px;
        width: 100%;
        margin: 0 auto;
        margin-bottom: 20px;
        font-size: 18px;
        line-height: 19px; } }
    .page-main__text:last-of-type {
      margin-bottom: 44px; }
    .page-main__text--bold {
      color: #000000; }
      @media (min-width: 768px) {
        .page-main__text--bold {
          color: #373737; } }
