.payment-form {
  &__button {
    display: block;

    max-width: 315px;
    width: 100%;
    margin: 0;
    padding: 13px;

    font-family: "SF UI Display", sans-serif;
    font-size: 18px;
    line-height: 19px;
    font-weight: 500;
    letter-spacing: 0.16px;
    color: #ffffff;

    border: none;
    background-color: #5BA550;
    border-radius: 54px;
    cursor: pointer;

    @media (min-width: $screen-tablet) {
      padding: 22px;
    }
  }
}
