.field-text {
  display: block;

  max-width: 315px;
  width: 100%;
  margin-bottom: 32px;

  font-family: "SF UI Display", sans-serif;

  @media (min-width: $screen-tablet) {
    max-width: 500px;
    margin-bottom: 40px;

    font-size: 18px;
    line-height: 23px;
    color: #000000;
  }

  &__input {
    display: block;

    width: 100%;
    margin-top: 24px;
    padding: 13px;

    font: inherit;

    border: none;
    background: #FBFBFB;
    box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.04);
    border-radius: 30px;

    @media (min-width: $screen-tablet) {
      margin-top: 10px;
      padding: 11px;

      box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.1);
    }
  }
}
